@keyframes primary {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.l-root {
  animation: 1.4s linear 0s infinite normal none running primary;
}

@keyframes secondary {
  0% {
    stroke-dasharray: 1px, 200px;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 100px, 200px;
    stroke-dashoffset: -15px;
  }
  100% {
    stroke-dasharray: 100px, 200px;
    stroke-dashoffset: -125px;
  }
}

.l-circle {
  stroke-dasharray: 80px, 200px;
  stroke-dashoffset: 0;
  animation: secondary 1.4s ease-in-out infinite;
}
